'use client';

import { Link } from '@/navigation';
import { useSession, signOut } from 'next-auth/react';
import { ShoppingCart, User, Search, Menu, Heart, X, ChevronDown, Coffee } from 'lucide-react';
import { useAppSelector, useAppDispatch } from '@/lib/redux/hooks';
import { openCart } from '@/lib/redux/features/cartSlice';
import { Button } from '@/components/ui/button';
import { CartDrawer } from '@/components/public/CartDrawer';
import { useWishlist } from '@/hooks/useWishlist';
import { useSettings } from '@/contexts/SettingsContext';
import { LanguageSwitcher } from '@/components/public/LanguageSwitcher';
import { SearchAutocomplete } from '@/components/public/SearchAutocomplete';
import { useState } from 'react';
import Image from 'next/image';
import { useTranslations } from 'next-intl';
import { useRouter } from '@/navigation';

export function Header() {
  const t = useTranslations();
  const { data: session } = useSession();
  const dispatch = useAppDispatch();
  const router = useRouter();
  const cartItems = useAppSelector((state) => state.cart.items);
  const [mobileMenuOpen, setMobileMenuOpen] = useState(false);
  const [searchOpen, setSearchOpen] = useState(false);
  const [searchQuery, setSearchQuery] = useState('');
  const [accountDropdownOpen, setAccountDropdownOpen] = useState(false);
  const { items: wishlistItems } = useWishlist();
  const { settings } = useSettings();

  const cartItemCount = cartItems.reduce((sum, item) => sum + item.quantity, 0);
  const wishlistItemCount = wishlistItems.length;

  const storeName = settings.general_store_name || 'Organicaf';
  const logo = settings.appearance_logo_url;

  return (
    <>
      <header className="bg-white shadow-sm sticky top-0 z-50">
        <div className="container mx-auto px-4">
          {/* Main Header */}
          <div className="flex items-center justify-between h-20">
            {/* Logo */}
            <Link href="/" className="flex items-center gap-2 group">
              {logo ? (
                <Image
                  src={logo}
                  alt={storeName}
                  width={140}
                  height={50}
                  className="h-10 w-auto object-contain"
                />
              ) : (
                <div className="flex items-center gap-2">
                  <Coffee className="w-8 h-8 text-amber-800 group-hover:text-amber-900 transition-colors" />
                  <div className="text-2xl font-bold text-amber-900 tracking-tight">
                    {storeName}
                  </div>
                </div>
              )}
            </Link>

            {/* Desktop Navigation */}
            <nav className="hidden lg:flex items-center gap-8">
              <Link
                href="/"
                className="text-gray-700 hover:text-amber-800 font-medium transition-colors"
              >
                {t('header.home')}
              </Link>
              <Link
                href="/shop"
                className="text-gray-700 hover:text-amber-800 font-medium transition-colors"
              >
                {t('header.shop')}
              </Link>
              <Link
                href="/about"
                className="text-gray-700 hover:text-amber-800 font-medium transition-colors"
              >
                {t('header.about')}
              </Link>
              <Link
                href="/contact"
                className="text-gray-700 hover:text-amber-800 font-medium transition-colors"
              >
                {t('header.contact')}
              </Link>
            </nav>

            {/* Right Side Actions */}
            <div className="flex items-center gap-2">
              {/* Search Icon */}
              <button
                onClick={() => setSearchOpen(true)}
                className="p-2.5 hover:bg-amber-50 rounded-lg transition-colors"
                aria-label={t('common.search')}
              >
                <Search className="w-5 h-5 text-gray-700" />
              </button>

              {/* Language Switcher */}
              <div className="hidden md:block">
                <LanguageSwitcher />
              </div>

              {/* Account */}
              <div className="hidden md:block relative">
                {session ? (
                  <div className="relative">
                    <button
                      onClick={() => setAccountDropdownOpen(!accountDropdownOpen)}
                      className="flex items-center gap-2 p-2 hover:bg-amber-50 rounded-lg transition-colors"
                    >
                      <div className="w-8 h-8 bg-gradient-to-br from-amber-600 to-amber-800 rounded-full flex items-center justify-center text-white text-sm font-semibold">
                        {session.user.name?.charAt(0).toUpperCase() || 'U'}
                      </div>
                    </button>

                    {accountDropdownOpen && (
                      <>
                        <div
                          className="fixed inset-0 z-10"
                          onClick={() => setAccountDropdownOpen(false)}
                        />
                        <div className="absolute right-0 mt-2 w-56 bg-white border border-gray-200 rounded-xl shadow-xl z-20 overflow-hidden">
                          <div className="px-4 py-3 border-b bg-amber-50">
                            <p className="text-sm font-semibold text-gray-900">{session.user.name}</p>
                            <p className="text-xs text-gray-500 truncate">{session.user.email}</p>
                          </div>
                          <div className="py-2">
                            <Link
                              href="/account"
                              className="flex items-center px-4 py-2.5 text-sm text-gray-700 hover:bg-amber-50 transition-colors"
                              onClick={() => setAccountDropdownOpen(false)}
                            >
                              <User className="w-4 h-4 mr-3 text-amber-700" />
                              {t('header.myAccount')}
                            </Link>
                            <Link
                              href="/account/orders"
                              className="flex items-center px-4 py-2.5 text-sm text-gray-700 hover:bg-amber-50 transition-colors"
                              onClick={() => setAccountDropdownOpen(false)}
                            >
                              <ShoppingCart className="w-4 h-4 mr-3 text-amber-700" />
                              {t('header.myOrders')}
                            </Link>
                            <Link
                              href="/wishlist"
                              className="flex items-center px-4 py-2.5 text-sm text-gray-700 hover:bg-amber-50 transition-colors"
                              onClick={() => setAccountDropdownOpen(false)}
                            >
                              <Heart className="w-4 h-4 mr-3 text-amber-700" />
                              {t('account.myWishlist')}
                            </Link>
                            {session.user.role === 'ADMIN' && (
                              <Link
                                href="/admin"
                                className="flex items-center px-4 py-2.5 text-sm text-amber-700 font-medium hover:bg-amber-50 transition-colors border-t"
                                onClick={() => setAccountDropdownOpen(false)}
                              >
                                {t('header.adminPanel')}
                              </Link>
                            )}
                          </div>
                          <div className="border-t">
                            <button
                              onClick={() => {
                                signOut();
                                setAccountDropdownOpen(false);
                              }}
                              className="w-full text-left px-4 py-2.5 text-sm text-red-600 hover:bg-red-50 transition-colors"
                            >
                              {t('header.signOut')}
                            </button>
                          </div>
                        </div>
                      </>
                    )}
                  </div>
                ) : (
                  <Link href="/auth/signin">
                    <Button variant="ghost" size="sm" className="gap-2 hover:bg-amber-50 hover:text-amber-900">
                      <User className="w-4 h-4" />
                    </Button>
                  </Link>
                )}
              </div>

              {/* Wishlist */}
              {session && (
                <Link
                  href="/wishlist"
                  className="relative p-2.5 hover:bg-amber-50 rounded-lg transition-colors hidden md:flex"
                >
                  <Heart className="w-5 h-5 text-gray-700" />
                  {wishlistItemCount > 0 && (
                    <span className="absolute -top-1 -right-1 bg-red-500 text-white text-xs rounded-full w-5 h-5 flex items-center justify-center font-semibold">
                      {wishlistItemCount}
                    </span>
                  )}
                </Link>
              )}

              {/* Cart */}
              <button
                onClick={() => dispatch(openCart())}
                className="relative p-2.5 hover:bg-amber-50 rounded-lg transition-colors"
              >
                <ShoppingCart className="w-5 h-5 text-gray-700" />
                {cartItemCount > 0 && (
                  <span className="absolute -top-1 -right-1 bg-amber-800 text-white text-xs rounded-full w-5 h-5 flex items-center justify-center font-semibold">
                    {cartItemCount}
                  </span>
                )}
              </button>

              {/* Mobile Menu Toggle */}
              <button
                className="lg:hidden p-2.5 hover:bg-amber-50 rounded-lg transition-colors min-w-[44px] min-h-[44px] flex items-center justify-center"
                onClick={() => setMobileMenuOpen(!mobileMenuOpen)}
                aria-label="Toggle menu"
                aria-expanded={mobileMenuOpen}
              >
                {mobileMenuOpen ? (
                  <X className="w-6 h-6 text-gray-700" />
                ) : (
                  <Menu className="w-6 h-6 text-gray-700" />
                )}
              </button>
            </div>
          </div>

          {/* Mobile Menu Overlay */}
          {mobileMenuOpen && (
            <>
              <div
                className="fixed inset-0 bg-black/50 z-40 lg:hidden transition-opacity duration-300"
                onClick={() => setMobileMenuOpen(false)}
              />
              <div className="fixed top-0 left-0 h-full w-80 max-w-[85vw] bg-white shadow-2xl z-50 lg:hidden transform transition-transform duration-300 ease-in-out">
                <div className="flex flex-col h-full">
                  {/* Mobile Menu Header */}
                  <div className="flex items-center justify-between p-4 border-b bg-amber-50">
                    <div className="flex items-center gap-2">
                      {logo ? (
                        <Image
                          src={logo}
                          alt={storeName}
                          width={120}
                          height={40}
                          className="h-8 w-auto object-contain"
                        />
                      ) : (
                        <div className="flex items-center gap-2">
                          <Coffee className="w-6 h-6 text-amber-800" />
                          <span className="text-lg font-bold text-amber-900">{storeName}</span>
                        </div>
                      )}
                    </div>
                    <button
                      onClick={() => setMobileMenuOpen(false)}
                      className="p-2 hover:bg-amber-100 rounded-lg transition-colors min-w-[44px] min-h-[44px] flex items-center justify-center"
                      aria-label="Close menu"
                    >
                      <X className="w-6 h-6 text-gray-700" />
                    </button>
                  </div>

                  {/* Mobile Menu Content */}
                  <nav className="flex-1 overflow-y-auto py-4">
                    <div className="flex flex-col space-y-1 px-2">
                      <Link
                        href="/"
                        className="px-4 py-3.5 text-gray-700 hover:bg-amber-50 hover:text-amber-900 rounded-lg font-medium transition-colors min-h-[44px] flex items-center"
                        onClick={() => setMobileMenuOpen(false)}
                      >
                        {t('header.home')}
                      </Link>
                      <Link
                        href="/shop"
                        className="px-4 py-3.5 text-gray-700 hover:bg-amber-50 hover:text-amber-900 rounded-lg font-medium transition-colors min-h-[44px] flex items-center"
                        onClick={() => setMobileMenuOpen(false)}
                      >
                        {t('header.shop')}
                      </Link>
                      <Link
                        href="/about"
                        className="px-4 py-3.5 text-gray-700 hover:bg-amber-50 hover:text-amber-900 rounded-lg font-medium transition-colors min-h-[44px] flex items-center"
                        onClick={() => setMobileMenuOpen(false)}
                      >
                        {t('header.about')}
                      </Link>
                      <Link
                        href="/contact"
                        className="px-4 py-3.5 text-gray-700 hover:bg-amber-50 hover:text-amber-900 rounded-lg font-medium transition-colors min-h-[44px] flex items-center"
                        onClick={() => setMobileMenuOpen(false)}
                      >
                        {t('header.contact')}
                      </Link>

                      {session ? (
                        <>
                          <div className="border-t my-2"></div>
                          <div className="px-4 py-2">
                            <div className="flex items-center gap-3 mb-2">
                              <div className="w-10 h-10 bg-gradient-to-br from-amber-600 to-amber-800 rounded-full flex items-center justify-center text-white text-sm font-semibold">
                                {session.user.name?.charAt(0).toUpperCase() || 'U'}
                              </div>
                              <div className="flex-1 min-w-0">
                                <p className="text-sm font-semibold text-gray-900 truncate">{session.user.name}</p>
                                <p className="text-xs text-gray-500 truncate">{session.user.email}</p>
                              </div>
                            </div>
                          </div>
                          <Link
                            href="/account"
                            className="px-4 py-3.5 text-gray-700 hover:bg-amber-50 hover:text-amber-900 rounded-lg transition-colors min-h-[44px] flex items-center"
                            onClick={() => setMobileMenuOpen(false)}
                          >
                            <User className="w-5 h-5 mr-3 text-amber-700" />
                            {t('header.myAccount')}
                          </Link>
                          <Link
                            href="/account/orders"
                            className="px-4 py-3.5 text-gray-700 hover:bg-amber-50 hover:text-amber-900 rounded-lg transition-colors min-h-[44px] flex items-center"
                            onClick={() => setMobileMenuOpen(false)}
                          >
                            <ShoppingCart className="w-5 h-5 mr-3 text-amber-700" />
                            {t('header.myOrders')}
                          </Link>
                          <Link
                            href="/wishlist"
                            className="px-4 py-3.5 text-gray-700 hover:bg-amber-50 hover:text-amber-900 rounded-lg transition-colors min-h-[44px] flex items-center justify-between"
                            onClick={() => setMobileMenuOpen(false)}
                          >
                            <div className="flex items-center">
                              <Heart className="w-5 h-5 mr-3 text-amber-700" />
                              <span>{t('account.myWishlist')}</span>
                            </div>
                            {wishlistItemCount > 0 && (
                              <span className="bg-red-500 text-white text-xs rounded-full px-2 py-1 font-semibold">
                                {wishlistItemCount}
                              </span>
                            )}
                          </Link>
                          {session.user.role === 'ADMIN' && (
                            <Link
                              href="/admin"
                              className="px-4 py-3.5 text-amber-700 font-medium hover:bg-amber-50 rounded-lg transition-colors min-h-[44px] flex items-center border-t mt-2 pt-2"
                              onClick={() => setMobileMenuOpen(false)}
                            >
                              {t('header.adminPanel')}
                            </Link>
                          )}
                          <button
                            onClick={() => {
                              signOut();
                              setMobileMenuOpen(false);
                            }}
                            className="px-4 py-3.5 text-red-600 hover:bg-red-50 rounded-lg transition-colors text-left font-medium min-h-[44px] flex items-center border-t mt-2"
                          >
                            {t('header.signOut')}
                          </button>
                        </>
                      ) : (
                        <>
                          <div className="border-t my-2"></div>
                          <Link
                            href="/auth/signin"
                            className="px-4 py-3.5 text-amber-700 font-medium hover:bg-amber-50 rounded-lg transition-colors min-h-[44px] flex items-center"
                            onClick={() => setMobileMenuOpen(false)}
                          >
                            <User className="w-5 h-5 mr-3" />
                            {t('header.signIn')}
                          </Link>
                        </>
                      )}

                      {/* Mobile Language Switcher */}
                      <div className="border-t my-2 px-4 py-2">
                        <LanguageSwitcher />
                      </div>
                    </div>
                  </nav>
                </div>
              </div>
            </>
          )}
        </div>
      </header>

      {/* Search Modal */}
      {searchOpen && (
        <div className="fixed inset-0 bg-black/50 z-50 flex items-start justify-center pt-12 md:pt-20 px-4 animate-in fade-in duration-200">
          <div
            className="absolute inset-0"
            onClick={() => {
              setSearchOpen(false);
              setSearchQuery('');
            }}
          />
          <div className="relative w-full max-w-2xl bg-white rounded-2xl shadow-2xl animate-in slide-in-from-top duration-300 max-h-[90vh] overflow-hidden flex flex-col">
            <div className="p-4 md:p-6 flex-shrink-0">
              <div className="flex items-center gap-3 md:gap-4 mb-4 md:mb-6">
                <Search className="w-5 h-5 md:w-6 md:h-6 text-amber-800 flex-shrink-0" />
                <SearchAutocomplete
                  value={searchQuery}
                  onChange={setSearchQuery}
                  onSubmit={(value) => {
                    router.push(`/shop?search=${encodeURIComponent(value)}`);
                    setSearchOpen(false);
                    setSearchQuery('');
                  }}
                  onClose={() => {
                    setSearchOpen(false);
                    setSearchQuery('');
                  }}
                  placeholder={t('header.searchPlaceholder')}
                />
                <button
                  onClick={() => {
                    setSearchOpen(false);
                    setSearchQuery('');
                  }}
                  className="p-2 hover:bg-gray-100 rounded-lg transition-colors min-w-[44px] min-h-[44px] flex items-center justify-center flex-shrink-0"
                  aria-label="Close search"
                >
                  <X className="w-5 h-5 text-gray-500" />
                </button>
              </div>
            </div>
          </div>
        </div>
      )}

      {/* Cart Drawer */}
      <CartDrawer />
    </>
  );
}
